.navbar-container {
    --navbar-height: 3rem;
    --navbar-width: 4.5rem;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    width: 100%;
    min-height: var(--navbar-height);
    height: var(--navbar-height);

    z-index: 1;
    position: fixed;
    top: 0;

    display: block;
    margin: 0;
    padding: 0;

    border-style: none none solid none;
    border-width: 1px;
    border-color: var(--navbar-border);
    -webkit-box-shadow: 0px 0px 6px black;
            box-shadow: 0px 0px 6px black;
  
    color: black;
    background-color: transparent;
}

.navbar-container ul {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;

    list-style-type: none;
}

.navbar-item {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    margin: 0;
    padding: 0;
}

.navbar-icon {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 2.25rem;
    height: auto;
    
    display: block;
    margin: 0;
    padding: 0.25rem;

    -webkit-filter: var(--navbar-icon-filter);

            filter: var(--navbar-icon-filter);
    -webkit-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    transition: opacity 0.4s;
    opacity: 0.6;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    -o-transition: transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.icon-text {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    text-align: center;
    font-size: 1.1em;
    font-weight: bold;

    opacity: 0;
    position: absolute;
    top: 3.5rem;

    font-family: 'Courier New', Courier, monospace;
    
    -webkit-transition: opacity 0.4s;
    
    -o-transition: opacity 0.4s;
    
    transition: opacity 0.4s;
    color: white;
}

.navbar-item:hover .navbar-icon {
    /*opacity: 0.25;*/
    -webkit-transform: scale(1.3, 1.3);
        -ms-transform: scale(1.3, 1.3);
            transform: scale(1.3, 1.3);
}

.navbar-item:hover .icon-text {
    opacity: 1;
}

.navbar-item.selected .navbar-icon {
    -webkit-filter: var(--navbar-icon-filter-hover);
            filter: var(--navbar-icon-filter-hover);
    -webkit-transform: scale(1.3, 1.3);
        -ms-transform: scale(1.3, 1.3);
            transform: scale(1.3, 1.3);
    opacity: 1;
}


/*landscape view - navbar on left side*/
@media only screen and (min-width: 960px) {
    .navbar-container {
        height: 100%;
        width: var(--navbar-width);
        min-width: var(--navbar-width);

        position: fixed;
        top: 0;
        left: 0;

        border-style: none solid none none;
    }

    .navbar-container ul {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }

    .navbar-item {
        margin: 2rem 0;
    }

    .navbar-icon {
        width: 2.8rem;
    }

    .icon-text {
        top: initial;
        left: var(--navbar-width);

        font-size: 1.25em;
    }
}


