.auto-text-container {
    --font-size: 1.8em; /*calc(1.5em + (2 - 1.5) * ((100vw - 25em) / (75 - 25)));*/
    --line-height: 1.5em;
    --cursor-size: var(--font-size);
    --cursor-spring-size: var(--font-size);
    --cursor-stand-up-size: var(--font-size);

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    max-width: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    display: inline-block;

    margin: 0;
    padding: 0.5rem 1rem;

    font-size: var(--font-size);
    font-weight: bold;
    font-family: monospace;
    line-height: var(--line-height);

    overflow-wrap: break-word;

    opacity: 0.85;
    background: transparent;
    background: gray;
}

.auto-text {
    display: inline-block;
}

.auto-text.spring {
    color: var(--accent-green);
    -webkit-transition: -webkit-transform 0.35s;
    transition: -webkit-transform 0.35s;
    -o-transition: transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
}

.auto-text.spring:hover {
    -webkit-transform: scale(1.4, 1.4);
        -ms-transform: scale(1.4, 1.4);
            transform: scale(1.4, 1.4);
}


@-webkit-keyframes stand-up {
    from {
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }

    to {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}


@keyframes stand-up {
    from {
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }

    to {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}
.auto-text.stand-up {
    display: inline-block;
    color: var(--accent-blue);

    

    -webkit-animation-name: stand-up;

    

            animation-name: stand-up;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}



@-webkit-keyframes cursor-blink {
    to {
        opacity: 1;
    }
}



@keyframes cursor-blink {
    to {
        opacity: 1;
    }
}

.auto-text-cursor {
    opacity: 0;
    font-size: var(--cursor-size);

    -webkit-animation-name: cursor-blink;

            animation-name: cursor-blink;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}

.auto-text-cursor.spring {
    color: var(--accent-green);
    font-size: var(--cursor-spring-size);
}

.auto-text-cursor.stand-up {
    font-size: var(--cursor-stand-up-size);
}


