@media only screen and (min-width: 960px) {
    #view-container {    
        display: -ms-flexbox;    
        display: flex;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
}





